@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
#navbar-main-container {
  width: 100%;
  display: flex;
  position: fixed;
  align-items: center;
  height: 50px;
  top: 0;
  justify-content: space-around;
  background-color: rgba(9, 11, 27, 0.9);
  z-index: 99;
}
#navbar-menu-container a:hover {
  color: rgb(170, 170, 170);
}
#navbar-menu-container {
  width: 50%;
  display: flex;
  justify-self: right;
  justify-content: space-between;
}

#navbar-menu-container a {
  color: white;
  text-decoration: none;
  font-size: 30px;
}

#navbar-links-container {
  width: 10%;
  margin-top: 1%;
  display: flex;
  justify-self: right;
  justify-content: space-evenly;
}

.social-icon-container {
  position: relative;
  margin-top: 15%;
  width: 50px;
  height: 50px;
}

.social-background {
  background-color: rgba(189, 189, 189, 0.301);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  position: absolute;
  transition: all 0.25s linear;
  top: -15%;
  left: -15%;
  -webkit-transform: scale(0);
          transform: scale(0);
  z-index: -1;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

.social-icons:hover {
  cursor: pointer;
}
.social-icons:hover + .social-background {
  -webkit-transform: scale(1);
          transform: scale(1);
}

@media (max-width: 1024px) {
  #minor-navbar {
    z-index: 99;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: fixed;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    right: -220px;
    top: 1%;
    transition: right 0.5s linear;
  }
  #menuIcon {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  #menuIcon:hover {
    cursor: pointer;
  }

  #navbar-main-container-min {
    padding: 7px 20px 0px 20px;
    display: flex;
    flex-direction: column;
    background-color: rgb(9, 18, 39, 0.99);
  }
  #navbar-menu-container-min {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #navbar-menu-container-min a {
    margin: 5% 0%;
  }

  #navbar-links-container-min {
    width: 100%;
    display: flex;
    justify-self: right;
    justify-content: space-evenly;
  }

  #navbar-menu-container-min a {
    color: white;
    text-decoration: none;
    font-size: 30px;
  }
}

.section-divider {
  width: 100%;
  height: 10px;
  background: linear-gradient(
    90deg,
    rgba(219, 71, 255, 0.5) 0%,
    rgba(0, 255, 252, 0.5) 100%
  );
  border-radius: 3px;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00fffc",endColorstr="#db47ff",GradientType=1);
}

