.gradient-text {
  background-image: linear-gradient(
    to right,
    rgba(219, 71, 255, 1) 0%,
    rgb(0, 183, 255) 50%,
    rgba(219, 71, 255, 1) 100%
  );
  background-size: 200% auto;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00fffc",endColorstr="#db47ff",GradientType=1);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: moveBackgroundColor 10s linear infinite;
          animation: moveBackgroundColor 10s linear infinite;
}

@-webkit-keyframes moveBackgroundColor {
  0% {
    background-position: left;
  }

  50% {
    background-position: right;
  }
}

@keyframes moveBackgroundColor {
  0% {
    background-position: left;
  }

  50% {
    background-position: right;
  }
}

.project-card-main-container {
  width: 450px;
  font-size: 20px;
  box-shadow: 0px 0px 5px 5px rgb(107, 208, 255);
  color: rgb(250, 202, 250);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
}

.project-card-divider {
  width: 75px;
  height: 5px;
  border-radius: 5px;
  background-color: aqua;
}

.project-card-buttons-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.project-card-tag-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

#project-description {
  padding: 5%;
}

