    width: 350px;
  }
}

.project-main-container {
  width: 100%;
  padding-top: 5%;
  margin-top: 10%;
}

.project-cards-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 0fr);
  grid-row-gap: 2em;
  grid-column-gap: 2em;
  justify-content: space-evenly;
}

@media (max-width: 768px) {
  .project-cards-wrapper {
    grid-template-columns: repeat(1, 0fr);
  }
}

#skill {
  padding-top: 5%;
  margin-top: 10%;
}

.skill-wrapper {
  margin-top: 5%;
  margin-bottom: 10%;
  display: grid;
  grid-template-columns: repeat(5, 0fr);
  justify-content: space-around;
  align-items: center;
  position: relative;
}

.skill-main-container p {
  width: 300px;
}

.skill-main-container p {
  color: white;
}

.vertical-divider {
  position: static;
  width: 5px;
  height: 60px;
  background-color: rgba(0, 255, 255, 0.74);
  border-radius: 5px;
}

.skill-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 250px;
}

@media (max-width: 1024px) {
  .skill-wrapper {
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(2, 0fr);
    grid-column-gap: 0px;
    -webkit-column-gap: 0px;
            column-gap: 0px;
    grid-row-gap: 50px;
    row-gap: 50px;
    justify-content: space-evenly;
    position: relative;
  }

  .vertical-divider {
    display: none;
  }
}

@media (max-width: 767px) {
  .skill-wrapper {
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(1, 0fr);
    justify-content: space-around;
    position: relative;
  }

  .vertical-divider {
    display: none;
  }
}

.experience-container {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 5%;
  margin-top: 10%;
  margin-bottom: 10%;
}
.experience-timeline-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.experience-timeline {
  margin-left: 2%;
  margin-top: 4%;
  width: 60%;
  height: 9px;
  background: rgb(0, 255, 252);
  background: linear-gradient(
    90deg,
    rgba(0, 255, 252, 1) 85%,
    rgba(219, 71, 255, 0) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00fffc",endColorstr="#db47ff",GradientType=1);
  position: relative;
}
.more-to-come {
  margin-top: 4%;
  margin-left: 1%;
  color: rgb(175, 175, 175);
}
.experience-progressbar {
  height: 4px;
  background-color: rgb(219, 71, 255);
  position: absolute;
  top: 25%;
  transition: width 1s linear;
}

.timeline-marker {
  position: absolute;
  left: 0%;
  top: -70%;
  width: 20px;
  height: 20px;
  background-color: rgb(255, 0, 0);
}
.timeline-marker:hover {
  -webkit-animation: rotation 6s linear infinite;
          animation: rotation 6s linear infinite;
}

.timeline-marker:hover::after {
  -webkit-animation: rotation 6s linear infinite, opacity 1s ease;
          animation: rotation 6s linear infinite, opacity 1s ease;
  opacity: 1;
}

.timeline-marker::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: rgb(255, 0, 0);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  opacity: 0;
}

.experience-title {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.experience-description-container {
  margin: 4% 2% 2% 2%;
  background: linear-gradient(
    0deg,
    rgba(0, 255, 252, 1) 0%,
    rgba(219, 71, 255, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00fffc",endColorstr="#db47ff",GradientType=1);
  padding: 2px 2px 2px 2px;
}

.experience-description li {
  font-family: "Oxygen", sans-serif;
  margin-bottom: 1%;
  color: white;
}

.experience-description {
  position: static;

  background-color: rgba(0, 0, 0, 0.7);
  -webkit-animation: changeOpacity 1s linear;
          animation: changeOpacity 1s linear;
  padding: 1%;
}

@media (max-width: 768px) {
  .experience-title {
    margin: 1%;
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }
}

@-webkit-keyframes changeOpacity {
  0%,
  100% {
    opacity: 1;
  }
  5% {
    opacity: 0.8;
  }
}

@keyframes changeOpacity {
  0%,
  100% {
    opacity: 1;
  }
  5% {
    opacity: 0.8;
  }
}

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

